.sentre-table-thead > tr > th {
  border-bottom: none !important;
}
.sentre-table-thead > tr > th::before {
  display: none;
}
.sentre-table-thead > tr:first-child > th:first-child {
  border-radius: 8px 0 0 8px;
}
.sentre-table-thead > tr:last-child > th:last-child {
  border-radius: 0 8px 8px 0;
}
.sentre-table-thead th {
  font-size: 12px;
  line-height: 20px;
  font-weight: 400 !important;
  color: #7a7b85 !important;
}
.sentre-table-tbody > tr > td {
  border-bottom: none !important;
}
.sentre-table-tbody > tr > td .action-icon {
  font-size: 24px;
}
.sentre-table-tbody > tr.sentre-table-row-level-0:hover > td {
  background-color: unset !important;
}
.sentre-table-tbody .even-row {
  background-color: #f4f4f5 !important;
}
.sentre-table-tbody .odd-row {
  background-color: #f9f9fa !important;
}
.sentre-table-tbody > tr > td:first-child {
  border-radius: 8px 0 0 8px;
}
.sentre-table-tbody > tr > td:last-child {
  border-radius: 0 8px 8px 0;
}
#dark .sentre-table-thead > tr > th {
  background-color: #242838 !important;
}
#dark .sentre-table-tbody .even-row {
  background-color: #212433 !important;
}
#dark .sentre-table-tbody .odd-row {
  background-color: #242838 !important;
}

.caption {
  font-size: 12px;
}
.card-swap {
  height: 338px;
}
.card-child {
  border-radius: 8px !important;
  box-shadow: unset !important;
}
.card-input {
  border: 1px solid;
}

#dark #sen_swap .card-child {
  background-color: #2c2e3d !important;
}
#dark #sen_swap .card-input {
  border-color: #212433;
}

#light #sen_swap .card-input {
  border-color: #7a7b85;
}
#light #sen_swap .card-child {
  background: #ffffff !important;
}

